* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 16px;
}

html {
    font-family: arial;
}

body {
    display: flex;
    justify-content: center;
}

.app {
    margin-top: 20vh;
}

.calc {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.calc__form {
    margin: 40px auto;
}

.choice__select{
    border: 2px solid rgb(110, 172, 207);
}

.form__input {
    border: 2px solid rgb(110, 172, 207);
    padding: 5px;
    text-align: center;
}

.choice__select:focus, .form__input:focus {
    outline: none;
    border: 2px solid rgb(3, 118, 212);
}

.form__button {
    display: block;
    margin: 40px auto 0;
    padding: 10px 15px;
    border: none;
    border-radius: 15px;
    background-color: rgb(17, 122, 184);
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}

.form__button:focus {
    outline: none;
}